<template>
  <div>
      <form novalidate class="md-layout" @reset.prevent="clearForm" @submit.prevent="validateUser">

    <md-content>    
    <div >
             <md-card md-with-hover>
      <md-card-header>
        <div class="md-title">Lot Number</div>
      </md-card-header>

      <md-card-content>
        <center><span class="md-display-2">{{ form.lotNum }}</span></center>
      </md-card-content>
    </md-card>
            </div>
      
      <md-tabs class="md-primary">
        <md-tab md-label="Edit Grave" style="height: 60vh;" >
          <div>
                  <div @click="launchFile1">
                  <md-avatar class="md-large md-avatar-icon" v-if="!form.down" >
                    <md-icon>add</md-icon>
                  <input type="file" 
                  ref="file1" 
                  :name="form.filename"
                  @change="changed1"
                  accept="image/*"
                  style="display: none;" />
                  </md-avatar>
                      <md-avatar class="md-large md-avatar-icon" v-if="form.down">
                          <img :src="form.down" alt="avatar">
                      </md-avatar>
                  </div>
                   
          </div>
          <div class="md-layout md-gutter">
            <div class="md-layout-item md-small-size-100">
              <md-field :class="getValidationClass('firstName')">
                <label for="first-name">First Name</label>
                <md-input name="first-name" id="first-name" autocomplete="given-name" v-model="form.firstName" :disabled="sending" />
                <span class="md-error" v-if="!$v.form.firstName.required">The first name is required</span>
                <span class="md-error" v-else-if="!$v.form.firstName.minlength">Invalid first name</span>
              </md-field>
            </div>

            <div class="md-layout-item md-small-size-100">
              <md-field :class="getValidationClass('lastName')">
                <label for="last-name">Last Name</label>
                <md-input name="last-name" id="last-name" autocomplete="family-name" v-model="form.lastName" :disabled="sending" />
                <span class="md-error" v-if="!$v.form.lastName.required">The last name is required</span>
                <span class="md-error" v-else-if="!$v.form.lastName.minlength">Invalid last name</span>
              </md-field>
            </div>
          </div>

          <div class="md-layout md-gutter">
            <div class="md-layout-item md-small-size-100">
              <md-field :class="getValidationClass('gender')">
                <label for="gender">Gender</label>
                <md-select name="gender" id="gender" v-model="form.gender" md-dense :disabled="sending">
                  <md-option></md-option>
                  <md-option value="Male">Male</md-option>
                  <md-option value="Female">Female</md-option>
                </md-select>
                <span class="md-error">The gender is required</span>
              </md-field>
            </div>

            <div class="md-layout-item md-small-size-100">
              <md-field :class="getValidationClass('age')">
                <label for="age">Age</label>
                <md-input type="number" id="age" name="age" autocomplete="age" v-model="form.age" :disabled="sending" />
                <span class="md-error" v-if="!$v.form.age.required">The age is required</span>
                <span class="md-error" v-else-if="!$v.form.age.maxlength">Invalid age</span>
              </md-field>
              </div>     
          </div>

          <div class="md-layout md-gutter">
            <div class="md-layout-item md-small-size-100">
          <label for="dateofbirth">Date of Birth</label>
          <md-field :class="getValidationClass('dateofbirth')">
            
            <md-input type="date" name="dateofbirth" id="dateofbirth" autocomplete="dateofbirth" v-model="form.dateofbirth" :disabled="sending" />
            <span class="md-error" v-if="!$v.form.dateofbirth.required">The date of birth is required</span>
            <span class="md-error" v-else-if="!$v.form.dateofbirth.dateofbirth">Invalid date</span>
          </md-field>
          </div>

            <div class="md-layout-item md-small-size-100">
          <label for="dateofdeath">Date of Death</label>
          <md-field :class="getValidationClass('dateofdeath')">
            <md-input type="date" name="dateofdeath" id="dateofdeath" autocomplete="dateofdeath" v-model="form.dateofdeath" :disabled="sending" />
            <span class="md-error" v-if="!$v.form.dateofdeath.required">The date of death is required</span>
            <span class="md-error" v-else-if="!$v.form.dateofdeath.dateofdeath">Invalid date</span>
          </md-field>
          </div>

            <div class="md-layout-item md-small-size-100"></div>
          </div>
       

        <md-progress-bar md-mode="indeterminate" v-if="sending"/>

        <md-dialog-alert
      :md-active.sync="active"
      md-title="Query Success!"
      :md-content="content"
      md-confirm-text="Close"
      @click="onConfirm" />

        </md-tab>
     
    


       
      <md-tab md-label="Edit Grave #2" v-if="count == 2"> 
        <div>
            <div @click="launchFile2">
                  <md-avatar class="md-large md-avatar-icon" v-if="!form2.down" >
                    <md-icon>add</md-icon>
                  <input type="file" 
                  ref="file2" 
                  :name="form2.filename"
                  @change="changed2"
                  accept="image/*"
                  style="display: none;" />
                  </md-avatar>
                      <md-avatar class="md-large md-avatar-icon" v-if="form2.down">
                          <img :src="form2.down" alt="avatar">
                      </md-avatar>
                  </div> 
              
          </div>
          <div class="md-layout md-gutter">
            <div class="md-layout-item md-small-size-100">
              <md-field :class="getValidationClass('firstName')">
                <label for="first-name">First Name</label>
                <md-input name="first-name" id="first-name" autocomplete="given-name" v-model="form2.firstName" :disabled="sending" />
                <span class="md-error" v-if="!$v.form.firstName.required">The first name is required</span>
                <span class="md-error" v-else-if="!$v.form.firstName.minlength">Invalid first name</span>
              </md-field>
            </div>

            <div class="md-layout-item md-small-size-100">
              <md-field :class="getValidationClass('lastName')">
                <label for="last-name">Last Name</label>
                <md-input name="last-name" id="last-name" autocomplete="family-name" v-model="form2.lastName" :disabled="sending" />
                <span class="md-error" v-if="!$v.form.lastName.required">The last name is required</span>
                <span class="md-error" v-else-if="!$v.form.lastName.minlength">Invalid last name</span>
              </md-field>
            </div>
          </div>

          <div class="md-layout md-gutter">
            <div class="md-layout-item md-small-size-100">
              <md-field :class="getValidationClass('gender')">
                <label for="gender">Gender</label>
                <md-select name="gender" id="gender" v-model="form2.gender" md-dense :disabled="sending">
                  <md-option></md-option>
                  <md-option value="Male">Male</md-option>
                  <md-option value="Female">Female</md-option>
                </md-select>
                <span class="md-error">The gender is required</span>
              </md-field>
            </div>

            <div class="md-layout-item md-small-size-100">
              <md-field :class="getValidationClass('age')">
                <label for="age">Age</label>
                <md-input type="number" id="age" name="age" autocomplete="age" v-model="form2.age" :disabled="sending" />
                <span class="md-error" v-if="!$v.form.age.required">The age is required</span>
                <span class="md-error" v-else-if="!$v.form.age.maxlength">Invalid age</span>
              </md-field>
              </div> 

              <div class="md-layout-item md-small-size-100">
            
            </div>     
          </div>

          <div class="md-layout md-gutter">
            <div class="md-layout-item md-small-size-100">
          <label for="dateofbirth">Date of Birth</label>
          <md-field :class="getValidationClass('dateofbirth')">
            
            <md-input type="date" name="dateofbirth" id="dateofbirth" autocomplete="dateofbirth" v-model="form2.dateofbirth" :disabled="sending" />
            <span class="md-error" v-if="!$v.form.dateofbirth.required">The date of birth is required</span>
            <span class="md-error" v-else-if="!$v.form.dateofbirth.dateofbirth">Invalid date</span>
          </md-field>
          </div>

            <div class="md-layout-item md-small-size-100">
          <label for="dateofdeath">Date of Death</label>
          <md-field :class="getValidationClass('dateofdeath')">
            <md-input type="date" name="dateofdeath" id="dateofdeath" autocomplete="dateofdeath" v-model="form2.dateofdeath" :disabled="sending" />
            <span class="md-error" v-if="!$v.form.dateofdeath.required">The date of death is required</span>
            <span class="md-error" v-else-if="!$v.form.dateofdeath.dateofdeath">Invalid date</span>
          </md-field>
          </div>

            <div class="md-layout-item md-small-size-100"></div>
          </div>
        

        <md-progress-bar md-mode="indeterminate" v-if="sending" />

        <md-dialog-alert
      :md-active.sync="active"
      md-title="Query Success!"
      :md-content="content"
      md-confirm-text="Close"
      @click="onConfirm" />

        </md-tab>
    </md-tabs>

      <div>
          
          <md-menu md-size="auto" md-direction="top-start"  >
          <md-button class="md-raised md-fab-bottom" @click="back" :disabled="sending">Back</md-button>
          <md-button md-menu-trigger class="md-raised md-accent md-fab-bottom" :disabled="sending">Remove Image</md-button>

            <md-menu-content>
            <md-menu-item @click="form.down = null" class="md-primary" v-if="form.down">Grave 1</md-menu-item>
            <md-menu-item @click="form2.down = null" class="md-primary" v-if="form2.down">Grave 2</md-menu-item>
          </md-menu-content>
          <md-button type="reset" class="md-raised md-accent md-fab-bottom" :disabled="sending">Reset Form</md-button>
          <md-button type="submit" class="md-raised md-primary md-fab-bottom" :disabled="sending">Update Data</md-button>
        </md-menu>
          
      </div>
    </md-content>
  </form>
  </div>
</template>

<script>
  import { validationMixin } from 'vuelidate'
  import firebase from 'firebase'
  import db from './firebaseInit'
  import {
    required,
    email,
    minLength,
    maxLength
  } from 'vuelidate/lib/validators'

  export default {
    name: 'edit-grave',
    mixins: [validationMixin],
    data: () => ({
      form: {
        firstName: null,
        lastName: null,
        gender: null,
        age: null,
        dateofdeath: null,
        dateofbirth: null,
        lotNum: null,
        filename: '',
        file: null,
        down: '',
        uploadTask: '',
        storageRef: null
      },
      form2: {
        firstName: null,
        lastName: null,
        gender: null,
        age: null,
        dateofdeath: null,
        dateofbirth: null,
        filename: '',
        file: null,
        down: '',
        uploadTask: '',
        storageRef: null
      },
      userSaved: false,
      sending: false,
      active: false,
      content: '',
      secGrave: false,
      count: 1
    }),
    validations: {
      form: {
        firstName: {
          required,
          minLength: minLength(3)
        },
        lastName: {
          required,
          minLength: minLength(3)
        },
        age: {
          required,
          maxLength: maxLength(3)
        },
        gender: {
          required
        },
        dateofdeath: {
          required
        },
        dateofbirth: {
          required
        }
      },
    },
    beforeRouteEnter (to, from, next) {
        db.collection('graves').where('lotNum', '==', to.params.lotNum).get()
        .then(querySnapshot => {
            querySnapshot.forEach(doc => {
                next(vm => {
                    vm.form.firstName = doc.data().firstName,
                    vm.form.lastName = doc.data().lastName,
                    vm.form.gender = doc.data().gender,
                    vm.form.age = doc.data().age,
                    vm.form.dateofdeath = doc.data().dateofdeath,
                    vm.form.dateofbirth = doc.data().dateofbirth,
                    vm.count = doc.data().count,
                    vm.form2.firstName = doc.data().firstName2,
                    vm.form2.lastName = doc.data().lastName2,
                    vm.form2.gender = doc.data().gender2,
                    vm.form2.age = doc.data().age2,
                    vm.form2.dateofdeath = doc.data().dateofdeath2,
                    vm.form2.dateofbirth = doc.data().dateofbirth2,
                    vm.form.lotNum = doc.data().lotNum,
                    vm.form.down = doc.data().down,
                    vm.form2.down = doc.data().down2
                })
            })
        })
    },
    watch: {
        '$route': 'fetchData'
    },
    methods: {
      getValidationClass (fieldName) {
        const field = this.$v.form[fieldName]

        if (field) {
          return {
            'md-invalid': field.$invalid && field.$dirty
          }
        }
      },
      clearForm () {
        this.$v.$reset()
        this.form.firstName = null
        this.form.lastName = null
        this.form.age = null
        this.form.gender = null
        this.form.dateofdeath = null
        this.form.lotNum = null
        this.form2.dateofbirth = null
        this.form2.firstName = null
        this.form2.lastName = null
        this.form2.age = null
        this.form2.gender = null
        this.form2.dateofdeath = null
        this.form2.dateofbirth = null
      },
      fetchData () {
            db.collection('graves').where('lotNum', '==', this.$route.params.lotNum)
            .get().then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    this.form.firstName = doc.data().firstName
                    this.form.lastName = doc.data().lastName
                    this.form.age = doc.data().age
                    this.form.gender = doc.data().gender
                    this.form.dateofdeath = doc.data().dateofdeath
                    this.form.dateofbirth = doc.data().dateofbirth
                    this.form.down = doc.data().down
                    this.form.lotNum = doc.data().lotNum
                    this.form2.firstName = doc.data().firstName2
                    this.form2.lastName = doc.data().lastName2
                    this.form2.age = doc.data().age2
                    this.form2.gender = doc.data().gender2
                    this.form2.dateofdeath = doc.data().dateofdeath2
                    this.form2.dateofbirth = doc.data().dateofbirth2
                    this.form2.down = doc.data().down2
                })
            })
        },
      updateGrave () {
            this.sending = true
        
            db.collection('graves').where('lotNum', '==', this.form.lotNum)
            .get().then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    doc.ref.update({
                        firstName: this.form.firstName,
                        lastName: this.form.lastName,
                        age: this.form.age,
                        gender: this.form.gender,
                        dateofdeath: this.form.dateofdeath,
                        dateofbirth: this.form.dateofbirth,
                        down: this.form.down,
                        occupied: true,
                        count: this.count
                    })
                    .then(setTimeout(() => {
                    this.userSaved = true
                    this.sending = false
                    this.active = true
                    this.content = 'The user '+ this.form.firstName + ' ' + this.form.lastName + ' was updated!'
                    this.clearForm()
                  }, 3000))
              })
            })
        },
        updateGraves () {
              this.sending = true
              db.collection('graves').where('lotNum', '==', this.form.lotNum)
            .get().then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    doc.ref.update({
                        firstName: this.form.firstName,
                        lastName: this.form.lastName,
                        age: this.form.age,
                        gender: this.form.gender,
                        dateofdeath: this.form.dateofdeath,
                        dateofbirth: this.form.dateofbirth,
                        down: this.form.down,
                        firstName2: this.form2.firstName,
                        lastName2: this.form2.lastName,
                        age2: this.form2.age,
                        gender2: this.form2.gender,
                        dateofdeath2: this.form2.dateofdeath,
                        dateofbirth2: this.form2.dateofbirth,
                        down2: this.form2.down,
                        occupied: true,
                        count: this.count
                    })
                    .then(setTimeout(() => {
                    this.userSaved = true
                    this.sending = false
                    this.active = true
                    this.content = 'The users '+ this.form.firstName + ' ' + this.form.lastName + ' and ' + this.form2.firstName + ' ' + this.form2.lastName + ' was updated!'
                    this.clearForm()
                  }, 3000))
              })
            })
        },
      validateUser () {
        this.$v.$touch()

        if (!this.$v.$invalid) {
          if(this.count == 1){
              this.updateGrave()
          }
          else if(this.count == 2){
              this.updateGraves()
          }
          
        }
      },
      onConfirm() {
          this.$router.push('/')
      },
      back() {
        this.$router.push('/adsearch')
      },
      launchFile1 () {
      this.$refs.file1.click()
    },
    changed1(event) {
      this.form.file = event.target.files[0]
      this.form.filename = event.target.files[0].name
      this.uploadImage1()
    },
    launchFile2 () {
      this.$refs.file2.click()
    },
    changed2(event) {
      this.form2.file = event.target.files[0]
      this.form2.filename = event.target.files[0].name
      this.uploadImage2()
    },
    uploadImage1 () {
        this.form.storageRef = firebase.storage().ref(this.form.filename)
        this.form.uploadTask = this.form.storageRef.put(this.form.file)

        this.form.uploadTask.on('state_changed', function(snapshot) {
          var progress = Math.floor(snapshot.bytesTransferred / snapshot.totalBytes * 100) ;
          console.log('Upload is ' + progress + '% done');
        },
        null,
        () => {
          this.form.uploadTask.snapshot.ref.getDownloadURL().then(downloadURL => {
              this.form.down = downloadURL
              console.log('downloadURL: ' +this.form.down)
          })
        })
    },
    uploadImage2 () {
        this.form2.storageRef = firebase.storage().ref(this.form2.filename)
        this.form2.uploadTask = this.form2.storageRef.put(this.form2.file)

        this.form2.uploadTask.on('state_changed', function(snapshot) {
          var progress = Math.floor(snapshot.bytesTransferred / snapshot.totalBytes * 100) ;
          console.log('Upload is ' + progress + '% done');
        },
        null,
        () => {
          this.form2.uploadTask.snapshot.ref.getDownloadURL().then(downloadURL => {
              this.form2.down = downloadURL
              console.log('downloadURL: ' +this.form2.down)
          })
        })
    },
    secForm () {
        this.secGrave = true
        this.count = 2
      },
      removeSec() {
        this.secGrave = false
        this.count = 1
      },
    }
  }
</script>

<style lang="scss" scoped>
  md-progress-bar {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
  }

</style>



