<template>
    <div>
        <md-card md-with-hover>
         
      <md-card-header>
        <div class="md-title">Login</div>
      </md-card-header>

      <md-card-content>
        <md-field>
                <md-icon>email</md-icon>
                <label>Username</label>
                <md-input v-model="email" @keydown.enter="login"></md-input>
            </md-field>
            <md-field>
                <md-icon>lock</md-icon>
                <label>Password</label>
                <md-input v-model="password" type="password" @keydown.enter="login"></md-input>
            </md-field>
      </md-card-content>

      <md-card-actions>
        <md-button class="md-raised md-primary" @click="login">Login</md-button>
      </md-card-actions>
     
    </md-card>
        <md-dialog-alert
      :md-active.sync="logged"
      :md-content="content"
      />

    </div>
</template>

<script>
import firebase from 'firebase'

export default {
    name: 'login',
    data: function() {
        return {
            email: '',
            password: '',
            logged: false,
            content: null
        }
    },
    methods: {
        login: function(e){
            firebase.auth().signInWithEmailAndPassword(this.email, this.password)
            .then(user => {
                alert('You are now logged in as ' + this.email)
                this.$router.go({path: this.$router.path})
            },
            err => {
                this.logged = true
                this.content = err.message
            })
        }
    }
}
    
</script>

<style lang="scss" scoped>
  .md-card {
    width: 400px;
    margin: 4px;
    display: inline-block;
    vertical-align: top;
    margin-left: 40%;
  }
</style>


