import Vue from 'vue'
import Router from 'vue-router'
import Dashboard from '@/components/Dashboard'
import NewGrave from '@/components/NewGrave'
import EditGrave from '@/components/EditG'
import Maps from '@/components/SVGContain'
import AdminMap from '@/components/AdminMap'
import TableSearch from '@/components/TableSearch'
import Dash from '@/components/Dash'
import Populate from '@/components/LotPopulate'
import Login from '@/components/Login'
import AdMaps from '@/components/AdMap'
import AdminTable from '@/components/AdminTable'
import firebase from 'firebase'

Vue.use(Router)

let router = new Router({
  mode: 'history',
  routes: [
    {
      path: '/s',
      name: 'dashboard',
      component: Dashboard,
      meta: {
        requiresGuest: true
      }
    },
    {
      path: '/dash',
      name: 'Dash',
      component: Dash,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/login',
      name: 'Login',
      component: Login,
      meta: {
        requiresGuest: true
      }
    },
    {
      path: '/new',
      name: 'new-grave',
      component: NewGrave,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/edit/:lotNum',
      name: 'edit-grave',
      component: EditGrave,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/map/:lotNum',
      name: 'Maps',
      component: Maps,
      meta: {
        requiresGuest: true
      }
    },
    {
      path: '/admap/:lotNum',
      name: 'AdMaps',
      component: AdMaps,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/admin',
      name: 'AdminMap',
      component: AdminMap,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/',
      name: 'TableSearch',
      component: TableSearch,
      meta: {
        requiresGuest: true
      }
    },
    {
      path: '/adsearch',
      name: 'AdminTable',
      component: AdminTable,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/test',
      name: 'TEST',
      component: Populate,
      meta: {
        requiresAuth: true
      }
    }
  ]
})

router.beforeEach((to, from ,next) => {
  if(to.matched.some(record => record.meta.requiresAuth)) {
    if(!firebase.auth().currentUser){
      next({
        path: '/',
        query: {
          redirect: to.fullPath
        }
      })
    }
    else {
      next()
    }
  }
  else if(to.matched.some(record => record.meta.requiresGuest)) {
    if(firebase.auth().currentUser){
      next({
        path: '/dash',
        query: {
          redirect: to.fullPath
        }
      })
    }
    else {
      next()
    }
  }
})

export default router


