<template>
    <div>
        <form @reset.prevent="clear" @submit.prevent="Hello" >
            <label>Lot Number: </label>
            <input type="number" v-model="lotNum"/>

            <label>Lot Position: </label>
            <input type="text" v-model="coords"/>

            <label>Route: </label>
            <input type="text" v-model="route"/>
            <button type="reset">Reset</button>
            <button type="submit">Submit</button>
        </form>
        
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    data () {
        return {
            lotNum: null,
            coords: null,
            route: null,
            active: false,
            age: null,
            
        }
    },
    methods: {
        Hello () {
        db.collection('graves').add({
        lotNum: this.lotNum,
        coordinates: this.coords,
        route: this.route,
        occupied: false
        }).then(this.clear())
      },
      clear() {
          this.lotNum = null
          this.coords = null
          this.route = null
      }
    }
}
</script>




