<template>
<div>
  <div class="page-container md-layout-column" v-if="loggedIn">
    <md-toolbar class="md-primary">
      <md-button class="md-icon-button" @click="showNavigation = true" v-show="loggedIn">
        <md-icon>menu</md-icon>
      </md-button>
  
      <span v-if="loggedIn" class="md-title">Admin Control Panel</span>
        <div class="container">
        <ul class="right">
          <li v-if="loggedIn">
              <span ><md-icon>perm_identity</md-icon>{{ currUser }}</span>
              <md-button v-if="loggedIn" class="md-icon-button md-dense" @click="logout">
              <md-icon>exit_to_app</md-icon>
              </md-button>
          </li>
        </ul>
        </div>
    </md-toolbar>

    <md-drawer :md-active.sync="showNavigation">
      <md-list>
        <router-link to="/">
          <md-list-item>
            <md-icon>home</md-icon>
            <span class="md-list-item-text" @click="showNavigation = false">Home</span>
          </md-list-item>
        </router-link>

        <router-link to="/new">
          <md-list-item v-if="loggedIn">
            <md-icon>add_location</md-icon>
            <span class="md-list-item-text" @click="showNavigation = false">Add</span>
          </md-list-item>
        </router-link>

        <router-link to="/adsearch">
          <md-list-item v-if="loggedIn">
            <md-icon>search</md-icon>
            <span class="md-list-item-text" @click="showNavigation = false">Search</span>
          </md-list-item>
        </router-link>

        <router-link to="/admin">
          <md-list-item v-if="loggedIn">
            <md-icon>map</md-icon>
            <span class="md-list-item-text" @click="showNavigation = false">Map CP</span>
          </md-list-item>
        </router-link>

        
        </md-list>
    </md-drawer>

    <md-content>
      <router-view></router-view>
    </md-content>
  </div>

  <div v-if="!loggedIn">
      <md-tabs md-sync-route class="md-primary" md-alignment="centered" md-active-tab="tab-home">
          <md-tab id="tab-home" md-label="Search" md-icon="search" @click="$router.push({ name: 'TableSearch' })">
                
      </md-tab>
      </md-tabs>

  </div>
</div>
</template>

<script>
import firebase from 'firebase'

  export default {
    name: 'Navbar',
    data: () => ({
        showNavigation: false,
        loggedIn: false,
        currUser: false
    }),
    methods: {
      logout () {
        firebase.auth().signOut().then(() => {
          this.$router.go({path: this.$router.path})
        })
      }
    },
    created() {
      if(firebase.auth().currentUser) {
        this.loggedIn = true;
        this.currUser = firebase.auth().currentUser.email
      }
    }
  }
</script>

<style lang="scss" scoped>
  .page-container {
    min-height: 300px;
    overflow: hidden;
    position: relative;
    border: 1px solid rgba(#000, .12);
  }

  .md-drawer {
    max-width: calc(100vw - 125px);
    height: 25vh;
    max-height: 25vh;
    width: 200px;
  }

  .md-content {
    padding: 16px;
  }
</style>



