<template>
    <div id="dashboard">
        <div class="md-layout md-alignment-center-center" id="content" v-if="loggedIn">

            <md-card>
      <md-card-header>
        <div class="md-title"><span class="md-display-2">Hello {{ currUser }} !</span></div>
      </md-card-header>

      <md-card-content>
            You can navigate through the control panel by clicking on the <md-icon class="md-size-2x">menu</md-icon> icon
            <br><br><md-icon class="md-size-2x">add_location</md-icon> is where you can add grave data where in you could 
            set specific lot number for the data
            <br><br><md-icon class="md-size-2x">search</md-icon> shows all the existing data where you can filter your search 
            via the search bar on the top right of the table.<br>
            <md-content style="margin-left: 10px; border: 1px solid black;">
            <div class="md-title">Additional Info on <md-icon class="md-size-1x">search</md-icon> page</div> <br>
            <md-button class="md-icon-button md-primary md-raised"> <md-icon>visibility</md-icon></md-button> lets you view addtional grave information, you can also manage specific data
            <br><br> <md-button class="md-icon-button md-accent md-raised"><md-icon>search</md-icon></md-button>
            redirects you to a map page where it shows the specific location of the grave <br>
            <p style="text-align: center;">Note: <i >You may have to zoom in on the map via mousewheel, double clicking, <br>or by using the panel on the bottom right</i></p>
            </md-content>

            <br><br><md-icon class="md-size-2x">map</md-icon> shows all occupied graves in the map, there you can also manage the data of each grave
      </md-card-content>

    
    </md-card>
        </div>

    </div>
</template>

<script>
import db from './firebaseInit'
import firebase from 'firebase'
export default {
    name: 'Dash',
    data () {
        return {
            loggedIn: false,
            currUser: null
        }
    },
    created () {
        if(firebase.auth().currentUser) {
        this.loggedIn = true;
        this.currUser = firebase.auth().currentUser.email
      }
    } 
}
</script>


<style lang="scss" scoped>
  .md-card {
    width: 55vw;
    margin: 4px;
    display: inline-block;
    vertical-align: top;
  }
</style>





