<template>
    <div id="dashboard">


        <div class="md-layout md-alignment-center-center" id="content" v-show="!loggedIn">
            <div class="md-layout-item md-size-100">
        <span class="md-display-2">Hello Guest! </span>
        <span class="md-display-1">
        <br><br><br><br>You can look for a loved one's grave by clicking on 
        <md-icon>search</md-icon> above, 
        <br><br><br>and enter their first or last name on the seach field</span> 
            </div>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'
import firebase from 'firebase'
export default {
    name: 'dashboard',
    data () {
        return {
            loggedIn: false,
            currUser: null
        }
    },
    created () {
        if(firebase.auth().currentUser) {
        this.loggedIn = true;
        this.currUser = firebase.auth().currentUser.email
      }
    } 
}
</script>

<style scoped>

#content{
    text-align: center;
    margin-top: 100px;
}
</style>





