<template>
  <div >
    <md-table v-model="searched" md-sort="name" md-sort-order="asc" md-card md-fixed-header id="tab">
      <md-table-toolbar>
        <div class="md-toolbar-section-start">
          <h1 class="md-title">Grave Data</h1>
        </div>
          
        <md-field md-clearable class="md-toolbar-section-end">
          <md-input placeholder="Search by name..." v-model="search" @input="searchOnTable" />
        </md-field>
      </md-table-toolbar>
            
      <md-table-empty-state 
        v-if="!query"
        md-icon="sentiment_very_dissatisfied"
        md-label="No data found"
        :md-description="`No data found for this '${search}' query. Try a different search term or add new data.`">
        
      </md-table-empty-state>
        <md-progress-bar md-mode="indeterminate" v-if="query" />
      <md-table-row slot="md-table-row" slot-scope="{ item }">
        <md-table-cell md-label="Picture"><md-avatar class="md-large md-avatar-icon md-list-action"><img :src="item.down"/></md-avatar></md-table-cell>
        <md-table-cell md-label="Full Name" md-sort-by="fullName">{{ item.fullName }}</md-table-cell>
        <md-table-cell md-label="Age" md-sort-by="age">{{ item.age }}</md-table-cell>
        <md-table-cell md-label="View Information" style="width: 320px;">
        <md-button class="md-icon-button md-primary md-raised"  @click="viewGrave(item.lotNum)">
            <md-icon>visibility</md-icon></md-button>
        </md-table-cell>
        <md-table-cell md-label="Search Grave" style="width: 320px;">  
          <md-button class="md-icon-button md-accent md-raised" @click="searchGrave(item.lotNum)">
            <md-icon>search</md-icon></md-button>
            </md-table-cell>
      </md-table-row>
    </md-table>
    <md-dialog :md-active.sync="active">
      <md-tabs md-dynamic-height>
        <md-tab md-label="Grave Data">

      <md-list>
        <md-list-item>
          <md-icon>insert_emoticon</md-icon>
          <span class="md-list-item-text">{{ tabledata.firstName }} {{ tabledata.lastName }}</span>
          <md-avatar class="md-large md-avatar-icon md-list-action"><img :src="tabledata.down"/></md-avatar>
        </md-list-item>

        <md-list-item>
          <md-icon>face</md-icon>
          <span class="md-list-item-text">{{ tabledata.age }}</span>
        </md-list-item>

        <md-list-item>
          <md-icon>people</md-icon>
          <span class="md-list-item-text">{{ tabledata.gender }}</span>
        </md-list-item>

        <md-list-item>
          <md-icon>cake</md-icon>
          <span class="md-list-item-text">{{ tabledata.dateofbirth }}</span>
        </md-list-item>

        <md-list-item>
          <md-icon>add</md-icon>
          <span class="md-list-item-text">{{ tabledata.dateofdeath }}</span>
        </md-list-item>

        <md-divider class="md-inset"></md-divider>
      </md-list>
        </md-tab>

        <md-tab md-label="Grave Data" v-if="count == 2">
          <md-list>
        <md-list-item>
          <md-icon>insert_emoticon</md-icon>
          <span class="md-list-item-text">{{ tabledata.firstName2 }} {{ tabledata.lastName2 }}</span>
          <md-avatar class="md-large md-avatar-icon md-list-action"><img :src="tabledata.down2"/></md-avatar>
        </md-list-item>

        <md-list-item>
          <md-icon>face</md-icon>
          <span class="md-list-item-text">{{ tabledata.age2 }}</span>
        </md-list-item>

        <md-list-item>
          <md-icon>people</md-icon>
          <span class="md-list-item-text">{{ tabledata.gender2 }}</span>
        </md-list-item>

        <md-list-item>
          <md-icon>cake</md-icon>
          <span class="md-list-item-text">{{ tabledata.dateofbirth2 }}</span>
        </md-list-item>

        <md-list-item>
          <md-icon>add</md-icon>
          <span class="md-list-item-text">{{ tabledata.dateofdeath2 }}</span>
        </md-list-item>

        <md-divider class="md-inset"></md-divider>
      </md-list>
        </md-tab>
      </md-tabs>

      <md-dialog-actions>
        <md-button class="md-plain md-raised" @click="active = false">Close</md-button>
      </md-dialog-actions>
    </md-dialog>
    <md-dialog-confirm
      :md-active.sync="del"
      md-title="Are you sure?"
      :md-content="content"
      md-confirm-text="Yes"
      md-cancel-text="No"
      @md-cancel="onCancel"
      @md-confirm="onConfirm" />
  </div>
</template>

<script>
import db from './firebaseInit'
import firebase from 'firebase'
  const toLower = text => {
    return text.toString().toLowerCase()
  }

  const searchByName = (items, term) => {
    if (term) {
      return items.filter(item => toLower(item.fullName).includes(toLower(term)))
    }

    return items
  }

  export default {
    name: 'TableSearch',
    data: () => ({
      search: null,
      query: false,
      searched: [],
      active: false,
      graves: [],
      tabledata: {
                firstName: null,
                lastName: null,
                age: null,
                gender: null,
                dateofbirth: null,
                dateofdeath: null,
                lotNum: null,
                firstName2: null,
                lastName2: null,
                age2: null,
                gender2: null,
                dateofbirth2: null,
                dateofdeath2: null,
                fullName: null,
                down: null,
                down2: null
            },
      count: 1,
      del: false,
      content: '',
      loggedIn: false
    }),
    methods: {
      searchOnTable () {
        this.searched = searchByName(this.graves, this.search)
      },
      searchGrave(value){
        this.$router.push({name: 'Maps', params: {lotNum: value}})
      },
      viewGrave(value){
       db.collection('graves').where('lotNum', '==', value)
                .get().then(querySnapshot => {
                    querySnapshot.forEach(doc =>{
                        this.count = doc.data().count
                    })
                })
    
        
        if(this.count == 1)
            {
            db.collection('graves').where('lotNum', '==', value)
            .get().then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    this.tabledata.firstName = doc.data().firstName
                    this.tabledata.lastName = doc.data().lastName
                    this.tabledata.age = doc.data().age
                    this.tabledata.gender = doc.data().gender
                    this.tabledata.dateofbirth = doc.data().dateofbirth
                    this.tabledata.dateofdeath = doc.data().dateofdeath
                    this.active = true
                    this.tabledata.lotNum = doc.data().lotNum
                    this.tabledata.fullName = this.tabledata.firstName + ' ' + this.tabledata.lastName
                    this.tabledata.down = doc.data().down
                })
            })
        }else if(this.count == 2){
                db.collection('graves').where('lotNum', '==', value)
            .get().then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    this.tabledata.firstName = doc.data().firstName
                    this.tabledata.lastName = doc.data().lastName
                    this.tabledata.age = doc.data().age
                    this.tabledata.gender = doc.data().gender
                    this.tabledata.dateofbirth = doc.data().dateofbirth
                    this.tabledata.dateofdeath = doc.data().dateofdeath
                    this.tabledata.firstName2 = doc.data().firstName2
                    this.tabledata.lastName2 = doc.data().lastName2
                    this.tabledata.age2 = doc.data().age2
                    this.tabledata.gender2 = doc.data().gender2
                    this.tabledata.dateofbirth2 = doc.data().dateofbirth2
                    this.tabledata.dateofdeath2 = doc.data().dateofdeath2
                    this.tabledata.down = doc.data().down
                    this.tabledata.down2 = doc.data().down2
                    this.active = true
                    this.tabledata.lotNum = doc.data().lotNum
                    this.tabledata.fullName = this.tabledata.firstName + ' ' + this.tabledata.lastName
                })
            })
        }
      },
      onConfirm () {
            db.collection('graves').where('lotNum', '==', this.tabledata.lotNum)
            .get().then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    doc.ref.delete()
                    this.$router.push('/')
                })
            }) 
      },
      onCancel () {

      }
    },
    created () {
      this.searched = this.graves
      this.query = true
        
        setTimeout(() =>{
            firebase.firestore().collection('graves').where('occupied', '==', true).get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
                const data = {
                    'firstName' : doc.data().firstName,
                    'lastName': doc.data().lastName,
                    'age': doc.data().age,
                    'gender': doc.data().gender,
                    'dateofbirth' : doc.data().dateofbirth,
                    'dateofdeath' : doc.data().dateofdeath,
                    'lotNum' : doc.data().lotNum,
                    'fullName' : doc.data().firstName + ' ' + doc.data().lastName,
                    'down' : doc.data().down
                }
                this.graves.push(data)
            })
        })
        firebase.firestore().collection('graves').where('count', '==', 2).get().then(querySnapshot => {
          querySnapshot.forEach(doc => {
            const data2 = {
                    'firstName' : doc.data().firstName2,
                    'lastName': doc.data().lastName2,
                    'age': doc.data().age2,
                    'gender': doc.data().gender2,
                    'dateofbirth' : doc.data().dateofbirth2,
                    'dateofdeath' : doc.data().dateofdeath2,
                    'lotNum' : doc.data().lotNum,
                    'fullName' : doc.data().firstName2 + ' ' + doc.data().lastName2,
                    'down': doc.data().down2
                }
                this.graves.push(data2)
          })
        })
            this.query = false
        }, 9000)
    }
  }
</script>

<style lang="scss" scoped>
  .md-field {
    max-width: 300px;
  }

  #tab {
    width: 80vw;
    overflow: none;
    margin-left: 6%;
  }
</style>



