<template>
<div>
  <div style="float: left; width: 50vw">
      <form novalidate class="md-layout" @reset.prevent="clearForm" @submit.prevent="validateUser">

    <md-content > 
      <div class="md-layout md-gutter">
        <div class="md-layout-item md-small-size-100">
              
            </div>
    <div class="md-layout-item md-small-size-25">
            <md-field :class="getValidationClass('lotNum')">
              <label for="lotNum">Lot Number</label>
              <md-select v-model="form.lotNum" required>
                <md-option v-for="Lot in lotArray" :key="Lot.index" :value="Lot.lotNum">{{ Lot.lotNum }}</md-option>
              </md-select>
              <span class="md-error">Lot Number is required</span>
            </md-field>
            </div>

            <div class="md-layout-item md-small-size-100">
              <md-button @click="showLot(form.lotNum)" class="md-raised md-primary" v-if="form.lotNum">View Lot</md-button>
              <md-button @click="showlot = false" class="md-raised md-accent" v-if="showlot">Close View</md-button>
            </div>
      </div>   
      <md-tabs class="md-primary" >
        <md-tab md-label="Add Grave" style="height: 60vh; ">
          <div >
                  <div @click="launchFile1">
                  <md-avatar class="md-large md-avatar-icon" v-if="!form.down" >
                    <md-icon>add</md-icon>
                  <input type="file" 
                  ref="file1" 
                  :name="form.filename"
                  @change="changed1"
                  accept="image/*"
                  style="display: none;" />
                  </md-avatar>
                      <md-avatar class="md-large md-avatar-icon" v-if="form.down">
                          <img :src="form.down" alt="avatar">
                      </md-avatar>
                  </div>    
          </div >
          <div class="md-layout md-gutter">
              <div class="md-layout-item md-small-size-100">
              <md-field :class="getValidationClass('firstName')">
                <label for="first-name">First Name</label>
                <md-input name="first-name" id="first-name" autocomplete="given-name" v-model="form.firstName" :disabled="sending" />
                <span class="md-error" v-if="!$v.form.firstName.required">The first name is required</span>
                <span class="md-error" v-else-if="!$v.form.firstName.minlength">Invalid first name</span>
              </md-field>
            </div>

            <div class="md-layout-item md-small-size-100">
              <md-field :class="getValidationClass('lastName')">
                <label for="last-name">Last Name</label>
                <md-input name="last-name" id="last-name" autocomplete="family-name" v-model="form.lastName" :disabled="sending" />
                <span class="md-error" v-if="!$v.form.lastName.required">The last name is required</span>
                <span class="md-error" v-else-if="!$v.form.lastName.minlength">Invalid last name</span>
              </md-field>
            </div>
            <div class="md-layout-item md-small-size-50">
              <md-field :class="getValidationClass('age')">
                <label for="age">Age</label>
                <md-input type="number" id="age" name="age" autocomplete="age"  v-model="form.age" @change="openDob" :disabled="sending" />
                <span class="md-error" v-if="!$v.form.age.required">The age is required</span>
                <span class="md-error" v-else-if="!$v.form.age.maxlength">Invalid age</span>
              </md-field>
              </div> 
               

        </div>
          <div class="md-layout md-gutter">
            <div class="md-layout-item md-small-size-100">
          <label for="dateofbirth">Date of Birth</label>
          <md-field :class="getValidationClass('dateofbirth')">
            <datepicker v-model="dob.year" :disabledDates="dob.disabledDates" :open-date="open1" @selected="setDob" :disabled="sending" ></datepicker>
            <span class="md-error" v-if="!$v.form.dateofbirth.required">The date of birth is required</span>
          </md-field>
          </div>  

              <div class="md-layout-item md-small-size-100">
          <label for="dateofbirth">Date of Death</label>
          <md-field :class="getValidationClass('dateofdeath')">
            <datepicker  v-model="dod.year" :disabledDates="dod.disabledDates" @selected="setDod"  :disabled="sending" ></datepicker>
            <span class="md-error" v-if="!$v.form.dateofdeath.required">The date of death is required</span>
          </md-field>
          </div>

          <div class="md-layout-item md-small-size-50">
              <md-field :class="getValidationClass('gender')">
                <label for="gender">Gender</label>
                <md-select name="gender" id="gender" v-model="form.gender" md-dense :disabled="sending">
                  <md-option></md-option>
                  <md-option value="Male">Male</md-option>
                  <md-option value="Female">Female</md-option>
                </md-select>
                <span class="md-error">The gender is required</span>
              </md-field>
              </div> 
            
          </div>


            
          
       

        <md-progress-bar md-mode="indeterminate" v-if="sending"/>

        <md-dialog-alert
      :md-active.sync="active"
      md-title="Query Success!"
      :md-content="content"
      md-confirm-text="Close"
      @click="onConfirm" />

        </md-tab>
     
      <md-tab md-label="Add Grave #2" v-if="secGrave" style="height: 60vh;"> 
        <div>
            <div><label>Image Upload </label></div>
            <div @click="launchFile2">
                  <md-avatar class="md-large md-avatar-icon" v-if="!form2.down" >
                    <md-icon>add</md-icon>
                  <input type="file" 
                  ref="file2" 
                  :name="form2.filename"
                  @change="changed2"
                  accept="image/*"
                  style="display: none;" />
                  </md-avatar>
                      <md-avatar class="md-large md-avatar-icon" v-if="form2.down">
                          <img :src="form2.down" alt="avatar">
                      </md-avatar>
                  </div> 
          </div>
          <div class="md-layout md-gutter">
            <div class="md-layout-item md-small-size-50">
              <md-field :class="getValidationClass('firstName')">
                <label for="first-name">First Name</label>
                <md-input name="first-name" id="first-name" autocomplete="given-name" v-model="form2.firstName" :disabled="sending" />
                <span class="md-error" v-if="!$v.form.firstName.required">The first name is required</span>
                <span class="md-error" v-else-if="!$v.form.firstName.minlength">Invalid first name</span>
              </md-field>
            </div>

            <div class="md-layout-item md-small-size-50">
              <md-field :class="getValidationClass('lastName')">
                <label for="last-name">Last Name</label>
                <md-input name="last-name" id="last-name" autocomplete="family-name" v-model="form2.lastName" :disabled="sending" />
                <span class="md-error" v-if="!$v.form.lastName.required">The last name is required</span>
                <span class="md-error" v-else-if="!$v.form.lastName.minlength">Invalid last name</span>
              </md-field>
            </div>

            <div class="md-layout-item md-small-size-50">
              <md-field :class="getValidationClass('age')">
                <label for="age">Age</label>
                <md-input type="number" id="age" name="age" autocomplete="age" v-model="form2.age" @change="openDob2"  :disabled="sending" />
                <span class="md-error" v-if="!$v.form.age.required">The age is required</span>
                <span class="md-error" v-else-if="!$v.form.age.maxlength">Invalid age</span>
              </md-field>
              </div> 
          </div>

          <div class="md-layout md-gutter">
            <div class="md-layout-item md-small-size-50">
          <label for="dateofbirth">Date of Birth</label>
          <md-field :class="getValidationClass('dateofbirth')">
            <datepicker v-model="dob2.year" :disabledDates="dob2.disabledDates" @selected="setDob2" :open-date="open2" :disabled="sending" ></datepicker>
            <span class="md-error" v-if="!$v.form.dateofbirth.required">The date of birth is required</span>
          </md-field>
          </div>  

              <div class="md-layout-item md-small-size-50">
          <label for="dateofbirth">Date of Death</label>
          <md-field :class="getValidationClass('dateofdeath')">
            <datepicker v-model="dod2.year" :disabledDates="dod2.disabledDates" @selected="setDod2" :disabled="sending" ></datepicker>
            <span class="md-error" v-if="!$v.form.dateofdeath.required">The date of death is required</span>
          </md-field>
          </div>

             <div class="md-layout-item md-small-size-50">
              <md-field :class="getValidationClass('gender')">
                <label for="gender">Gender</label>
                <md-select name="gender" id="gender" v-model="form2.gender" md-dense :disabled="sending">
                  <md-option></md-option>
                  <md-option value="Male">Male</md-option>
                  <md-option value="Female">Female</md-option>
                </md-select>
                <span class="md-error">The gender is required</span>
              </md-field>
            </div>
          </div>
        

        <md-progress-bar md-mode="indeterminate" v-if="sending" />

        <md-dialog-alert
      :md-active.sync="active"
      md-title="Query Success!"
      :md-content="content"
      md-confirm-text="Close"
      @click="onConfirm" />

        </md-tab>
    </md-tabs>

      <div>
          <md-menu md-size="auto" md-direction="top-start">
          <md-button class="md-raised md-mini md-fab md-fab-bottom" @click="secForm"  :disabled="secGrave || sending"><md-icon>add</md-icon></md-button>
          <md-button class="md-raised md-fab md-mini md-fab-bottom" @click="removeSec" :disabled="sending || count == 1 "><md-icon>remove</md-icon></md-button>
          
           <md-button md-menu-trigger class="md-accent md-raised" :disabled="sending" v-if="form.down || form2.down">Remove Image</md-button>

            <md-menu-content>
            <md-menu-item @click="form.down = null" class="md-primary" v-if="form.down">Grave 1</md-menu-item>
            <md-menu-item @click="form2.down = null" class="md-primary" v-if="form2.down">Grave 2</md-menu-item>
          </md-menu-content>
        
          <md-button type="reset" class="md-raised md-accent md-fab-bottom" :disabled="sending">Reset Form</md-button>
          <md-button type="submit" class="md-raised md-primary md-fab-bottom" :disabled="sending">Add Data</md-button>
          </md-menu>
      </div>
    </md-content>
  </form>
