          <text transform="matrix(-6.556708e-008 1.5 -1 -4.371139e-008 1208.165 335.1499)" font-family="'MyriadPro-Bold'" font-size="14" letter-spacing="4.667"> </text>
        <text transform="matrix(-6.556708e-008 1.5 -1 -4.371139e-008 1194.165 346.543)" font-family="'MyriadPro-Bold'" font-size="14">O</text>
        
          <text transform="matrix(-6.556708e-008 1.5 -1 -4.371139e-008 1208.165 351.9497)" font-family="'MyriadPro-Bold'" font-size="14" letter-spacing="4.667"> </text>
        
          <text transform="matrix(-6.556708e-008 1.5 -1 -4.371139e-008 1194.165 365.5054)" font-family="'MyriadPro-Bold'" font-size="14" letter-spacing="1.333">L</text>
        
          <text transform="matrix(-6.556708e-008 1.5 -1 -4.371139e-008 1208.165 368.75)" font-family="'MyriadPro-Bold'" font-size="14" letter-spacing="4.667"> </text>
        <text transform="matrix(-6.556708e-008 1.5 -1 -4.371139e-008 1194.165 381.3394)" font-family="'MyriadPro-Bold'" font-size="14">Y</text>
        
          <text transform="matrix(-6.556708e-008 1.5 -1 -4.371139e-008 1208.165 385.5493)" font-family="'MyriadPro-Bold'" font-size="14" letter-spacing="4.667"> </text>
        
          <text transform="matrix(-6.556708e-008 1.5 -1 -4.371139e-008 1194.165 402.3491)" font-family="'MyriadPro-Bold'" font-size="14" letter-spacing="4.667"> </text>
        
          <text transform="matrix(-6.556708e-008 1.5 -1 -4.371139e-008 1194.165 412.3872)" font-family="'MyriadPro-Bold'" font-size="14" letter-spacing="-1.333">M</text>
        
          <text transform="matrix(-6.556708e-008 1.5 -1 -4.371139e-008 1208.165 419.1499)" font-family="'MyriadPro-Bold'" font-size="14" letter-spacing="4.667"> </text>
        <text transform="matrix(-6.556708e-008 1.5 -1 -4.371139e-008 1194.165 431.1821)" font-family="'MyriadPro-Bold'" font-size="14">A</text>
        
          <text transform="matrix(-6.556708e-008 1.5 -1 -4.371139e-008 1208.165 435.9497)" font-family="'MyriadPro-Bold'" font-size="14" letter-spacing="4.667"> </text>
        <text transform="matrix(-6.556708e-008 1.5 -1 -4.371139e-008 1194.165 448.644)" font-family="'MyriadPro-Bold'" font-size="14">R</text>
        
          <text transform="matrix(-6.556708e-008 1.5 -1 -4.371139e-008 1208.165 452.7505)" font-family="'MyriadPro-Bold'" font-size="14" letter-spacing="4.667"> </text>
        <text transform="matrix(-6.556708e-008 1.5 -1 -4.371139e-008 1194.165 465.3394)" font-family="'MyriadPro-Bold'" font-size="14">Y</text>
        
          <text transform="matrix(-6.556708e-008 1.5 -1 -4.371139e-008 1208.165 469.5493)" font-family="'MyriadPro-Bold'" font-size="14" letter-spacing="4.667"> </text>
        
          <text transform="matrix(-6.556708e-008 1.5 -1 -4.371139e-008 1194.165 486.3491)" font-family="'MyriadPro-Bold'" font-size="14" letter-spacing="4.667"> </text>
        <text transform="matrix(-6.556708e-008 1.5 -1 -4.371139e-008 1194.165 499.0229)" font-family="'MyriadPro-Bold'" font-size="14">C</text>
        
          <text transform="matrix(-6.556708e-008 1.5 -1 -4.371139e-008 1208.165 503.1499)" font-family="'MyriadPro-Bold'" font-size="14" letter-spacing="4.667"> </text>
        <text transform="matrix(-6.556708e-008 1.5 -1 -4.371139e-008 1194.165 514.8364)" font-family="'MyriadPro-Bold'" font-size="14">H</text>
        
          <text transform="matrix(-6.556708e-008 1.5 -1 -4.371139e-008 1208.165 519.9497)" font-family="'MyriadPro-Bold'" font-size="14" letter-spacing="4.667"> </text>
        <text transform="matrix(-6.556708e-008 1.5 -1 -4.371139e-008 1194.165 531.9829)" font-family="'MyriadPro-Bold'" font-size="14">A</text>
        
          <text transform="matrix(-6.556708e-008 1.5 -1 -4.371139e-008 1208.165 536.7505)" font-family="'MyriadPro-Bold'" font-size="14" letter-spacing="4.667"> </text>
        <text transform="matrix(-6.556708e-008 1.5 -1 -4.371139e-008 1194.166 549.5708)" font-family="'MyriadPro-Bold'" font-size="14">P</text>
        
          <text transform="matrix(-6.556708e-008 1.5 -1 -4.371139e-008 1208.166 553.5493)" font-family="'MyriadPro-Bold'" font-size="14" letter-spacing="4.667"> </text>
        
          <text transform="matrix(-6.556708e-008 1.5 -1 -4.371139e-008 1194.166 566.8638)" font-family="'MyriadPro-Bold'" font-size="14" letter-spacing="0.667">E</text>
        
          <text transform="matrix(-6.556708e-008 1.5 -1 -4.371139e-008 1208.166 570.3491)" font-family="'MyriadPro-Bold'" font-size="14" letter-spacing="4.667"> </text>
        
          <text transform="matrix(-6.556708e-008 1.5 -1 -4.371139e-008 1194.165 583.9048)" font-family="'MyriadPro-Bold'" font-size="14" letter-spacing="1.333">L</text>
        
          <text transform="matrix(-0.4661 1.0863 -0.919 -0.3943 1839.998 640.3213)" font-family="'MyriadPro-Bold'" font-size="16.6909" letter-spacing="1.692">ST. JOSEPH AVENUE</text>
        
          <text transform="matrix(-0.019 1.4761 -0.9999 -0.0129 1671.0098 1296.5186)" font-family="'MyriadPro-Regular'" font-size="15" letter-spacing="2.032">ST. THERESE</text>
        
          <text transform="matrix(-0.9385 0.5604 -0.5127 -0.8586 1577.8428 1289.9839)" font-family="'MyriadPro-Regular'" font-size="11.9993" letter-spacing="0.915">ST. GABRIEL</text>
        
          <text transform="matrix(-0.4161 1.7196 -0.9719 -0.2352 1251.5498 980.5693)" font-family="'MyriadPro-Regular'" font-size="19.9991" letter-spacing="3.391">H  O  L  Y</text>
        
          <text transform="matrix(-0.8438 1.6267 -0.8877 -0.4604 1154.3857 1256.8579)" font-family="'MyriadPro-Regular'" font-size="20" letter-spacing="3.82">M  A  R  Y</text>
        
          <text transform="matrix(0.8176 0.5758 -0.5758 0.8176 1417.04 1351.1592)" font-family="'MyriadPro-Regular'" font-size="10" letter-spacing="2">HOLY</text>
        
          <text transform="matrix(0.0304 0.9995 -0.9995 0.0304 1452.0332 1380.9927)" font-family="'MyriadPro-Regular'" font-size="9.9996" letter-spacing="2">MARY</text>
        
          <text transform="matrix(-0.6262 0.7797 -0.7797 -0.6262 1452.0313 1420.4287)" font-family="'MyriadPro-Regular'" font-size="10" letter-spacing="2">CIRCLE</text>
        
          <text transform="matrix(-0.0011 0.8625 -1 -0.0013 1393.876 1351.5)" font-family="'MyriadPro-Bold'" font-size="17.5832" letter-spacing="2.319">HOLY MARY </text>
        
          <text transform="matrix(-0.0011 0.8625 -1 -0.0013 1372.751 1370.5195)" font-family="'MyriadPro-Bold'" font-size="17.5832" letter-spacing="2.319">STATUE</text>
        <text transform="matrix(1 0 0 1 771.4224 645.5293)" font-family="'MyriadPro-Regular'" font-size="15" letter-spacing="3">ST. FRANCIS</text>
      </g>   
			<g>
			<path :d="path" id="grave"></path>
			</g>	 
        </svg>

      </SvgPanZoom>
  </div>
</div>
</template>

<script>
  import { validationMixin } from 'vuelidate'
  import Datepicker from 'vuejs-datepicker'
  import SvgPanZoom from 'vue-svg-pan-zoom'
  import firebase from 'firebase'
  import db from './firebaseInit'
  import {
    required,
    minLength,
    maxLength
  } from 'vuelidate/lib/validators'

  export default {
    name: 'new-grave',
    mixins: [validationMixin],
    components: {
    Datepicker,SvgPanZoom
    },
    data: () => ({
      form: {
        firstName: null,
        lastName: null,
        gender: null,
        age: null,
        dateofdeath: null,
        dateofbirth: null,
        lotNum: null,
        filename: '',
        file: null,
        down: '',
        uploadTask: '',
        storageRef: null,
        
      },
      form2: {
        firstName: null,
        lastName: null,
        gender: null,
        age: null,
        dateofdeath: null,
        dateofbirth: null,
        filename: '',
        file: null,
        down: '',
        uploadTask: '',
        storageRef: null,
        
      },
      userSaved: false,
      sending: false,
      active: false,
      content: '',
      lotArray: [],
      secGrave: false,
      count: 1,
      showlot: false,
      path: null,
      open1: null,
      open2: null,
      dob: {
                year: null,
                disabledDates: {
                        from: new Date(),
	                            }
                  },
      dod: {
                year: null,
                disabledDates: {
                        from: new Date(),
                                }
                },
      dob2: {
                year: null,
                disabledDates: {
                        from: new Date(),
	                            }
                  },
      dod2: {
                year: null,
                disabledDates: {
                        from: new Date(),
                                }
                }
    }),
    validations: {
      form: {
        firstName: {
          required,
          minLength: minLength(3)
        },
        lastName: {
          required,
          minLength: minLength(3)
        },
        age: {
          required,
          maxLength: maxLength(3)
        },
        gender: {
          required
        },
        dateofdeath: {
          required
        },
        dateofbirth: {
          required
        },
        lotNum: {
          required
        }
      },
    },
    created () {
        db.collection('graves').where('occupied', '==', false ).get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
                const data = {
                    'lotNum' : doc.data().lotNum
                }
                this.lotArray.push(data)
            })
        })
    },
    methods: {
      getValidationClass (fieldName) {
        const field = this.$v.form[fieldName]

        if (field) {
          return {
            'md-invalid': field.$invalid && field.$dirty
          }
        }
      },
      clearForm () {
        this.$v.$reset()
        this.form.firstName = null
        this.form.lastName = null
        this.form.age = null
        this.form.gender = null
        this.dob.year = null
        this.form.down = null
        this.form.lotNum = null
        this.dod.year = null
        this.form2.firstName = null
        this.form2.lastName = null
        this.form2.age = null
        this.form2.gender = null
        this.dob2.year = null
        this.dod2.year = null
        this.form2.down = null
        this.showlot = false
      },
      updateGrave () {
            this.sending = true
        
            db.collection('graves').where('lotNum', '==', this.form.lotNum)
            .get().then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    doc.ref.update({
                        firstName: this.form.firstName,
                        lastName: this.form.lastName,
                        age: this.form.age,
                        gender: this.form.gender,
                        dateofdeath: this.form.dateofdeath,
                        dateofbirth: this.form.dateofbirth,
                        occupied: true,
                        count: this.count,
                        down: this.form.down
                    })
                    .then(setTimeout(() => {
                    this.userSaved = true
                    this.sending = false
                    this.active = true
                    this.content = 'The user '+ this.form.firstName + ' ' + this.form.lastName + ' was added!'
                    this.clearForm()
                  }, 3000))
              })
            })
        },
        updateGraves () {
              this.sending = true
              db.collection('graves').where('lotNum', '==', this.form.lotNum)
            .get().then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    doc.ref.update({
                        firstName: this.form.firstName,
                        lastName: this.form.lastName,
                        age: this.form.age,
                        gender: this.form.gender,
                        dateofdeath: this.form.dateofdeath,
                        dateofbirth: this.form.dateofbirth,
                        down: this.form.down,
                        firstName2: this.form2.firstName,
                        lastName2: this.form2.lastName,
                        age2: this.form2.age,
                        gender2: this.form2.gender,
                        dateofdeath2: this.form2.dateofdeath,
                        dateofbirth2: this.form2.dateofbirth,
                        occupied: true,
                        count: this.count,
                        down2: this.form2.down
                    })
                    .then(setTimeout(() => {
                    this.userSaved = true
                    this.sending = false
                    this.active = true
                    this.content = 'The users '+ this.form.firstName + ' ' + this.form.lastName + ' and ' + this.form2.firstName + ' ' + this.form2.lastName + ' was added!'
                    this.clearForm()
                  }, 3000))
              })
            })
        },
      validateUser () {
        this.$v.$touch()

        if (!this.$v.$invalid) {
          if(this.count == 1){
              this.updateGrave()
          }
          else if(this.count == 2){
              this.updateGraves()
          }
          
        }
      },
      
      setDob(date) {
         var year = date.getFullYear()
          var day = date.getDate()
          var month = date.getMonth()
        if(month < 9){
          if(day < 10){
            var full = year +'-0'+(month+1)+'-0'+day
            }
          else{
            var full = year +'-0'+(month+1)+'-'+day
          }
        }else{
          if(day < 10){
            var full = year +'-'+(month+1)+'-0'+day
            }
          else{
            var full = year +'-'+(month+1)+'-'+day
          } 
        }

        this.form.dateofbirth = full
        
      },
      setDob2(date) {
         var year = date.getFullYear()
          var day = date.getDate()
          var month = date.getMonth()
         if(month < 9){
            if(day < 10){
              var full = year +'-0'+(month+1)+'-0'+day
              }
            else{
              var full = year +'-0'+(month+1)+'-'+day
            }
          }else{
            if(day < 10){
              var full = year +'-'+(month+1)+'-0'+day
              }
            else{
              var full = year +'-'+(month+1)+'-'+day
            } 
          }

          this.form2.dateofbirth = full
      },
      setDod(date) {
         var year = date.getFullYear()
          var day = date.getDate()
          var month = date.getMonth()
        if(month < 9){
          if(day < 10){
            var full = year +'-0'+(month+1)+'-0'+day
            }
          else{
            var full = year +'-0'+(month+1)+'-'+day
          }
        }else{
          if(day < 10){
            var full = year +'-'+(month+1)+'-0'+day
            }
          else{
            var full = year +'-'+(month+1)+'-'+day
          } 
        }

        this.form.dateofdeath = full
        
      },
      setDod2(date) {
         var year = date.getFullYear()
          var day = date.getDate()
          var month = date.getMonth()
         if(month < 9){
            if(day < 10){
              var full = year +'-0'+(month+1)+'-0'+day
              }
            else{
              var full = year +'-0'+(month+1)+'-'+day
            }
          }else{
            if(day < 10){
              var full = year +'-'+(month+1)+'-0'+day
              }
            else{
              var full = year +'-'+(month+1)+'-'+day
            } 
          }

          this.form2.dateofdeath = full
      },
      showLot(value){
        db.collection('graves').where('lotNum', '==', value)
            .get().then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    this.path = doc.data().coordinates
                    this.showlot = true
                })
            })
      },
      onConfirm() {
          this.$router.push('/')
      },
      secForm () {
        this.secGrave = true
        this.count = 2
      },
      removeSec() {
        this.secGrave = false
        this.count = 1
      },
      launchFile1 () {
      this.$refs.file1.click()
    },
    changed1(event) {
      this.form.file = event.target.files[0]
      this.form.filename = event.target.files[0].name
      this.uploadImage1()
    },
    launchFile2 () {
      this.$refs.file2.click()
    },
    changed2(event) {
      this.form2.file = event.target.files[0]
      this.form2.filename = event.target.files[0].name
      this.uploadImage2()
    },
    uploadImage1 () {
        this.form.storageRef = firebase.storage().ref(this.form.filename)
        this.form.uploadTask = this.form.storageRef.put(this.form.file)

        this.form.uploadTask.on('state_changed', function(snapshot) {
          var progress = Math.floor(snapshot.bytesTransferred / snapshot.totalBytes * 100) ;
          console.log('Upload is ' + progress + '% done');
        },
        null,
        () => {
          this.form.uploadTask.snapshot.ref.getDownloadURL().then(downloadURL => {
              this.form.down = downloadURL
              console.log('downloadURL: ' +this.form.down)
          })
        })
    },
    uploadImage2 () {
        this.form2.storageRef = firebase.storage().ref(this.form2.filename)
        this.form2.uploadTask = this.form2.storageRef.put(this.form2.file)

        this.form2.uploadTask.on('state_changed', function(snapshot) {
          var progress = Math.floor(snapshot.bytesTransferred / snapshot.totalBytes * 100) ;
          console.log('Upload is ' + progress + '% done');
        },
        null,
        () => {
          this.form2.uploadTask.snapshot.ref.getDownloadURL().then(downloadURL => {
              this.form2.down = downloadURL
              console.log('downloadURL: ' +this.form2.down)
          })
        })
    },
      openDob () {
        
        var year = new Date().getFullYear() - this.form.age
        
        this.open1 = new Date((year-1), 0, 1)
      },
      openDob2 () {
        
        var year = new Date().getFullYear() - this.form2.age
        
        this.open2 = new Date((year-1), 0, 1)
      },
    }
  }
</script>

<style lang="scss" scoped>
  md-progress-bar {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
  }

#grave{
	fill: orange;
	stroke: black;
	stroke-width: 2px;
}
</style>



